import React, { useState, useEffect } from 'react'
import { 
    Icon, 
    Grid,
    Select,
    MenuItem
} from '@mui/material';
import { Span } from 'app/components/Typography'
import Button from '@mui/material/Button'
// import TextField from '@mui/material/TextField'
import Dialog from '@mui/material/Dialog'
import DialogTitle from '@mui/material/DialogTitle'
import { styled } from '@mui/system'
import { ValidatorForm, TextValidator, SelectValidator } from 'react-material-ui-form-validator'
import SimpleForm from '../material-kit/forms/SimpleForm';
import moment from 'moment';
import { createCenter } from 'app/services/userService';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import './AddCenterModal.css';

const TextField = styled(TextValidator)(() => ({
    width: '100%',
    marginBottom: '16px',
}))

const SelectField = styled(SelectValidator)(() => ({
    width: '100%',
    marginBottom: '16px'
}))

const StyledButton = styled(Button)(({ theme }) => ({
    margin: theme.spacing(1),
}))

export default function AddCenterModal(props) {
    const [open, setOpen] = React.useState(false)
    const [userInfo, setUserInfo] = useState({
        firstName: '',
        lastName: '',
        email: '',
        phone_number: '',
        country: 'country',
        state: 'state',
        city: '',
        postal_code: '',
        street_address1: '',
        street_address2: '',
        username: '',
        password: '',
        confirmPassword: '',
        logo: null,
        logo_image: ''
    })

    useEffect(() => {
        if(props && props.openModal) {
            setUserInfo({
                firstName: '',
                lastName: '',
                email: '',
                phone_number: '',
                country: 'country',
                state: 'state',
                city: '',
                postal_code: '',
                street_address1: '',
                street_address2: '',
                username: '',
                password: '',
                confirmPassword: '',
                logo: null,
                logo_image: ''
            })
            setOpen(true);
        }
    }, [props]);

    useEffect(() => {
        ValidatorForm.addValidationRule('isSelectedCountry', (value) => {
            if (value === 'country') {
                return false;
            }
            return true;
        });

        return () => {
            ValidatorForm.removeValidationRule('isSelectedCountry');
        }
    }, [userInfo.country]);

    useEffect(() => {
        ValidatorForm.addValidationRule('isSelectedState', (value) => {
            if (value === 'state') {
                return false;
            }
            return true;
        });

        return () => {
            ValidatorForm.removeValidationRule('isSelectedState');
        }
    }, [userInfo.state]);

    useEffect(() => {
        ValidatorForm.addValidationRule('isPasswordMatch', (value) => {
            if (value !== userInfo.password) {
                return false;
            }
            return true;
        })

        return () => {
            ValidatorForm.removeValidationRule('isPasswordMatch');
        }
    }, [userInfo.confirmPassword]);

    function handleClose() {
        setOpen(false)
        props.closeModal();
    }

    function handleChange(event) {
        event.persist()
        setUserInfo({
            ...userInfo,
            [event.target.name]: event.target.value,
        })
    }
 
    const handleFileUpload = (e) => {
        const file = e.target.files[0];
        setUserInfo({...userInfo, logo: file, logo_image: file.name });
    }

    const uploadFile = async(file) => {
        const formData = new FormData();
        formData.append('files', file);
        
        await fetch('http://localhost:1337/api/upload', {
            method: 'post',
            body: formData
        });
    }
    
    const handleSubmit = async (event) => {

        if(userInfo.logo){
            await uploadFile(userInfo.logo);
        }

        let options = {
            center: {
                data: {
                    name: userInfo.firstName + ' ' + userInfo.lastName,
                    first_name: userInfo.firstName,
                    last_name: userInfo.lastName,
                    email: userInfo.email,
                    phone_number: userInfo.phone_number,
                    country: userInfo.country,
                    state: userInfo.state,
                    city: userInfo.city,
                    street_address1: userInfo.street_address1,
                    street_address2: userInfo.street_address2,
                    postal_code: userInfo.postal_code,
                    logo_image: userInfo.logo_image,
                    publishedAt: moment().format().split('T')[0] + ' ' + moment().format().split('T')[1].slice(0,8)
                }
            },
            user: {
                data: {
                    username: userInfo.username,
                    email: userInfo.email,
                    password: userInfo.password,
                    confirmed: true,
                    blocked: false,
                    role: 5
                }
            }
        };

        createCenter(options)
        .then(resp => {
            if(resp && resp.status === 'success'){
                toast.success('Center created successfully', {
                    theme: 'colored'
                })
                setTimeout(() => {
                    handleClose();
                }, 3000);
            } else {
                toast.error('Something went wrong', {
                    theme: 'colored'
                });
            }
        }).catch(err => {
            toast.error('Something went wrong', {
                theme: 'colored'
            });
        })
    }

    const {
        firstName,
        lastName,
        email,
        phone_number,
        country,
        state,
        city,
        postal_code,
        street_address1,
        street_address2,
        username,
        password,
        confirmPassword
    } = userInfo;


    return (
        <div>
            <Dialog
                open={open}
                onClose={handleClose}
                aria-labelledby="form-dialog-title"
            >
                <DialogTitle id="form-dialog-title">Add Center</DialogTitle>
                {/* <DialogContent> */}
                <ValidatorForm onSubmit={handleSubmit} onError={() => null}>
                <div className="create__form-wrap">
                    <TextField
                        autoFocus
                        margin="dense"
                        id="first_name"
                        name="firstName"
                        label="First Name"
                        type="text"
                        value={firstName}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="last_name"
                        name="lastName"
                        label="Last Name"
                        type="text"
                        value={lastName}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="email"
                        name="email"
                        label="Email"
                        type="email"
                        value={email}
                        onChange={handleChange}
                        fullWidth
                        validators={['required', 'isEmail']}
                        errorMessages={[
                            'This field is required',
                            'email is not valid',
                        ]}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="phone_number"
                        name="phone_number"
                        label="Phone Number"
                        type="text"
                        value={phone_number}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <SelectField
                        autoFocus 
                        size="large"
                        margin="dense"
                        id="country"
                        name="country"
                        value={country}
                        onChange={(e) => setUserInfo({ ...userInfo, country: e.target.value})} 
                        defaultValue={'country'}
                        fullWidth
                        validators={['isSelectedCountry']}
                        errorMessages={['This field is required']}
                    >
                        <MenuItem value="country">Select Country</MenuItem>
                        <MenuItem value="usa">United States</MenuItem>
                        <MenuItem value="canada">Canada</MenuItem>
                        <MenuItem value="england">England</MenuItem>
                    </SelectField>
                    <SelectField
                        autoFocus 
                        size="large"
                        margin="dense"
                        id="state"
                        name="state"
                        value={state}
                        onChange={(e) => setUserInfo({ ...userInfo, state: e.target.value})} 
                        defaultValue={'state'}
                        fullWidth
                        validators={['isSelectedState']}
                        errorMessages={['This field is required']}
                    >
                        <MenuItem value="state">Select State</MenuItem>
                        <MenuItem value="usa">California</MenuItem>
                        <MenuItem value="canada">Los Angeles</MenuItem>
                        <MenuItem value="england">New Mexico</MenuItem>
                    </SelectField>
                    <TextField
                        id="city"
                        autoFocus
                        margin="dense"
                        name="city"
                        label="City"
                        type="text"
                        value={city}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="street_address1"
                        name="street_address1"
                        label="Street Address1"
                        type="text"
                        value={street_address1}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="street_address2"
                        name="street_address2"
                        label="Street Address2 (Optional)"
                        type="text"
                        value={street_address2}
                        onChange={handleChange}
                        fullWidth
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="postal_code"
                        name="postal_code"
                        label="Postal Code"
                        type="text"
                        value={postal_code}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="username"
                        name="username"
                        label="Username"
                        type="text"
                        value={username}
                        onChange={handleChange}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="password"
                        label="Password"
                        onChange={handleChange}
                        name="password"
                        type="password"
                        value={password || ''}
                        fullWidth
                        validators={['required']}
                        errorMessages={['This field is required']}
                    />
                    <TextField
                        autoFocus
                        margin="dense"
                        id="confirmPassword"
                        label="Confirm Password"
                        onChange={handleChange}
                        name="confirmPassword"
                        type="password"
                        value={confirmPassword || ''}
                        validators={['required', 'isPasswordMatch']}
                        errorMessages={[
                            'This field is required',
                            "password didn't match",
                        ]}
                    />
                    <Button
                        variant="contained"
                        component="label"
                        >
                        Upload File
                        <input
                            type="file"
                            hidden
                            accept="image/*"
                            multiple
                            onChange={handleFileUpload}
                        />
                    </Button>
                </div>
                <Button className="btn-wrap" color="primary" variant="contained" type="submit">
                    <Span sx={{ pl: 1, textTransform: 'capitalize' }}>
                        Submit
                    </Span>
                </Button>    
                </ValidatorForm>
                <ToastContainer
                    position="top-center"
                    autoClose={3000}
                    hideProgressBar={true}
                    newestOnTop={false}
                />
            </Dialog>
        </div>
    )
}
