const Analytics = () => {
    const { palette } = useTheme()
    const { user } = useAuth();
    const navigate = useNavigate();
    const [modalVisible, setModalVisible] = useState(false);
    const [modal1Visible, setModal1Visible] = useState(false);
    const [modal2Visible, setModal2Visible] = useState(false);
    const [addModalVisible, setAddModalVisible] = useState(false);
    const [data, setData] = useState({})
    const [session, setSession] = useState([])
    const [sessionId, setSessionId] = useState([])
    const [loading, setLoading] = useState(true)
    const [page, setPage] = useState(0)
    const [rowsPerPage, setRowsPerPage] = React.useState(10)
    // const [modalVisible, setModalVisible] = useState(false)
    const [open, setOpen] = useState(false);
    const [msg, setMsg] = useState('');

    useEffect(() => {
        getDashBoardData(user);
    }, [])

    const getSessionList = (id) => {
        setLoading(true)
        getCounsellorsDetails(id).then((res) => {
            if (res) {
                let data = []
                const td_start = moment().startOf('day')
                const td_end = moment().endOf('day')
                res.sessions.map((item) => {
                    if (moment(moment(item.scheduled_at).format('x'), 'x').isBetween(td_start, td_end)) {
                        const resp = item.status === 'pending' && checkTimeDiffBetween(item, res.sessions) || false
                        item.disabled = resp.length > 0 ? true : false
                        data.push(item)
                    }
                })
                data.sort(function (a, b) {
                    return new Date(a.updatedAt) - new Date(b.updatedAt)
                })
                setSession(formatSession(data,'upcoming'))
                setLoading(false)
            }
        }).catch(err => console.log(err))
    }

    const fetchData = () => {
        const res = getDashBoardData(user)
        return res
    }

    const handleChangePage = (event, newPage) => {
        setPage(newPage)
        // if (!searchResult) {
        try {
            let pageNumber = (Number(newPage)) === 0 ? 0 : (Number(newPage) - 1) + 10
            getCounsellorsDetails(pageNumber)
                .then((resp) => {
                    setSession(formatSession(resp))
                    // setRootRows(resp)
                })
                .catch(err => {
                    console.log("ERROR", err)
                })
        } catch (error) {
            console.log("ERROR", error)
        }
        // }
    }

    const handleChangeRowsPerPage = (event) => {
        setRowsPerPage(+event.target.value)
        setPage(0)
    }

    const getDashBoardData = async (user) => {
        if (user && user.role && user.role.type === 'admin') {
            let response = await getAdminDashboard();
            setLoading(false);
            if(response?.error?.name === 'UnauthorizedError'){
                setOpen(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                if(response?.status === 'fail') {
                    setOpen(true);
                    setMsg(response.message);
                } else if(response?.status === 'success'){
                    const [res] = response?.data;
                    setData(res);
                }
            }
        } else if (user && user.role && user.role.type === 'center') {
            const resp = await getUserDetails(user?.id);
            if(resp?.error?.name === 'UnauthorizedError'){
                setLoading(false);
                setOpen(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                const res = await getCenterDashboard(resp?.center?.id);
                setLoading(false);
                if(res?.error?.name === 'UnauthorizedError'){
                    setOpen(true);
                    setMsg('Your session has expired. You will be logged out.');
                } else {
                    if(res.status === 'fail'){
                        setOpen(true);
                        setMsg(res.message);
                    } else if(res.status === 'success'){
                        const arr = res?.data;
                        setData(arr[0]);
                    }
                }
                    
            }    
        } else if (user && user.role && user.role.type === 'counsellor') {
            const resp = await getUserDetails(user?.id);
            console.log('resp', resp);
            if(resp?.error?.name === 'UnauthorizedError'){
                setLoading(false);
                setOpen(true);
                setMsg('Your session has expired. You will be logged out.');
            } else {
                const res = await getCounsellorDashboard(resp.counsellor.id);
                console.log('res', res);
                setLoading(false);
                if(res?.error?.name === 'UnauthorizedError'){
                    setOpen(true);
                    setMsg('Your session has expired. You will be logged out.');
                } else {
                    if(res.status === 'fail'){
                        setOpen(true);
                        setMsg(res.message);
                    } else if(res.status === 'success'){
                        const arr = res?.data;
                        setData(arr[0]);
                    }
                    getSessionList(resp.counsellor.id);
                }
            }
        }
    }
   

    const handleNewNotes = (id, visible) => {
        setSessionId(id);
        setAddModalVisible(visible);
    }

    const handleSubmitNotes = (value) => {
        setLoading(true)
        let options = {
            data: {
                details: value,
                added_by: user.counsellor.name,
                sessions: sessionId.sessionId
            }
        }

        createNotes(options)
            .then(resp => {
                if (resp) {
                    toast.success('Notes created successfully', {
                        theme: 'colored'
                    })
                    setLoading(false)
                    handleNewNotes(null, false)
                }
            })
            .catch(err => console.log(err))
    }

    

    if (user && user.role && user.role.type === 'admin' && !loading) {
        return (
            <Fragment>
                <h1 style={{ marginLeft: '27px' }}>Dashboard</h1>
                <ContentBox className="analytics" sx={{ height: '85vh', display: 'flex', justifyContent: 'space-between' }}>
                    <Grid container spacing={3}>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            <StatCards admin={true} data={data} loading={loading} />
                        </Grid>

                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            <Grid container spacing={3}>
                                <Grid item xs={12} md={4}>
                                    <StyledButton onClick={() => navigate('/centers/add')}>
                                        <div>Add Center</div>
                                    </StyledButton>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <StyledButton onClick={() => navigate('/counsellors/add', { breadcrumb: true })}>
                                        <div>Add Provider</div>
                                    </StyledButton>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <StyledButton onClick={() => navigate('/devices/add')}>
                                        <div>Add Device</div>
                                    </StyledButton>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </ContentBox>
                <AddCenterModal
                    openModal={modalVisible}
                    closeModal={() => setModalVisible(false)}
                />
                <AddCounsellorModal
                    openModal={modal1Visible}
                    closeModal={() => setModal1Visible(false)}
                />
                <AddDeviceModal
                    openModal={modal2Visible}
                    closeModal={() => setModal2Visible(false)}
                />
                <SessionModal
                    open={open}
                    text={msg}
                    onConfirmDialogClose={() => {
                        setMsg('');
                        setOpen(false);
                    }}
                    onYesClick={() => {
                        setMsg('');
                        setOpen(false);
                    }}
                />
            </Fragment>
        )
    } else if (user && user.role && user.role.type === 'center' && !loading) {
        return (
            <Fragment>
                <h1 style={{ marginLeft: '27px' }}>Dashboard</h1>
                <ContentBox className="analytics" sx={{ height: '85vh', display: 'flex', justifyContent: 'space-between' }}>
                    <Grid container spacing={3}>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            <StatCards center={true} data={data} loading={loading} />
                        </Grid>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            <Grid container spacing={3}>
                                <Grid item xs={12} md={4}>
                                    <StyledCenterButton onClick={() => navigate('/clients/add')}>
                                        <div>New Client</div>
                                    </StyledCenterButton>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <StyledCenterButton onClick={() => navigate('/clients')}>
                                        <div>Assign Devices/License</div>
                                    </StyledCenterButton>
                                </Grid>
                                <Grid item xs={12} md={4}>
                                    <StyledCenterButton onClick={() => setModal2Visible(true)}>
                                        <div>Return Device/Expire license</div>
                                    </StyledCenterButton>
                                </Grid>
                            </Grid>
                        </Grid>
                    </Grid>
                </ContentBox>
                <SessionModal
                    open={open}
                    text={msg}
                    onConfirmDialogClose={() => {
                        setMsg('');
                        setOpen(false);
                    }}
                    onYesClick={() => {
                        setMsg('');
                        setOpen(false);
                    }}
                />
            </Fragment>
        )
    } else if (user && user.role && user.role.type === 'counsellor' && !loading) {
        return (
            <Fragment>

                {/* <h1 style={{ marginLeft: '27px' }}>Dashboard</h1> */}
                <ContentBox className="analytics" sx={{ height: '20vh', display: 'flex', justifyContent: 'space-between' }}>
                    <Grid container spacing={2}>
                        <Grid item lg={12} md={12} sm={12} xs={12}>
                            <h1>Dashboard</h1>
                            <StatCards counsellor={true} data={data} loading={loading} />
                        </Grid>
                        <Grid item>
                            <Grid container>
                                <h1>Today's Appointments</h1>
                            </Grid>
                            {!loading &&
                                session.meta &&
                                // <Grid container mb={10}>
                                     <AppointmentListTable 
                                        role={user.role.name}
                                        data={session}
                                        loading={loading}
                                        fetchData={fetchData}
                                        user={user}
                                        handleOpenNewNotes={handleNewNotes}
                                        addNotes={true}
                                    /> 
                                }
                        </Grid>
                    </Grid>
                </ContentBox>
                <AddNewNotes
                    open={addModalVisible}
                    data={sessionId}
                    handleClose={() => {
                        handleNewNotes(null, false)
                    }}
                    loading={loading}
                    handleSubmit={handleSubmitNotes}
                />
                <ToastContainer
                    position="top-center"
                    autoClose={3000}
                    hideProgressBar={true}
                    newestOnTop={false}
                />
                <SessionModal
                    open={open}
                    text={msg}
                    onConfirmDialogClose={() => {
                        setMsg('');
                        setOpen(false);
                    }}
                    onYesClick={() => {
                        setMsg('');
                        setOpen(false);
                    }}
                />
            </Fragment>
        )
    } else {
        return (
            <Fragment>
                {/* <ContentBox className="analytics">
                    <Grid container spacing={3}>
                        <Grid item lg={8} md={8} sm={12} xs={12}>
                            <StatCards />
                            <TopSellingTable />
                            <StatCards2 />
                            <H4>Ongoing Projects</H4>
                            <RowCards />
                        </Grid>

                        <Grid item lg={4} md={4} sm={12} xs={12}>
                            <Card sx={{ px: 3, py: 2, mb: 3 }}>
                                <Title>Traffic Sources</Title>
                                <SubTitle>Last 30 days</SubTitle>
                                <DoughnutChart
                                    height="300px"
                                    color={[
                                        palette.primary.dark,
                                        palette.primary.main,
                                        palette.primary.light,
                                    ]}
                                />
                            </Card>
                            <UpgradeCard />
                            <Campaigns />
                        </Grid>
                    </Grid>
                </ContentBox> */}
                <Loading />
            </Fragment>
        )
    }
}

export default Analytics
